import React, { useContext, useState } from 'react';
import { AdminHeader, AdminSidebar } from 'src/components';
import AllFirms_logo from 'src/assets/images/admin_dashboard_icons/AllFirms.png';
import { Form, FormControl, FormGroup, Row, Col, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { APIAdmin } from 'src/api/APIAdmin';
import { UserContext } from 'src/context/UserContextProvider';
import 'src/scss/_custom.scss';

const Create_New_Firm = () => {
    const { user } = useContext(UserContext);
    const userId = user.userId;

    const [firmName, setFirmName] = useState('');
    const [firmAddress, setFirmAddress] = useState('');
    const [firmGstno, setFirmGstno] = useState('');
    const [firmEmail, setFirmEmail] = useState('');

    const [successMessage, setSuccessMessage] = useState('');
    const [errorMessage, setErrorMessage] = useState('');

    const create_firm = () => {
        APIAdmin().CREATE_FIRM(userId, {
            firm_name: firmName,
            firm_email: firmEmail,
            firm_gstno: firmGstno,
            firm_address: firmAddress,
            firm_status: "1"
        }).then((response) => {

            setSuccessMessage('Firm added successfully!!');
            setErrorMessage('');
            setTimeout(() => {
                window.location.reload();
            }, 1000);
        }).catch((error) => {
            console.error("Please fill out details again.", error);
            setErrorMessage('Please fill out details again.');
            setSuccessMessage('');
            setTimeout(() => {
                window.location.reload();
            }, 1000);
        });
    };

    return (
        <div>
            <AdminSidebar />
            <div className="wrapper d-flex flex-column min-vh-100 bg-light">
                <AdminHeader />
                <div className="body flex-grow-1 px-5">
                    <Row className="justify-content-center mb-3">
                        <Col xs={12} md={10} lg={8}>
                            <div className="card_border_color_change py-5" style={{ borderColor: "white" }}>
                                <Row className="allfirms_title_mainrow">
                                    <Col xs={8} style={{ marginLeft: "10px" }}>
                                        <Row className="allfirms_title_row py-1 align-items-center">
                                            <Col xs={1}>
                                                <img src={AllFirms_logo} width={25} height={25} alt="All Firms Logo" />
                                            </Col>
                                            <Col xs={4}>
                                                <h4>Create Firm</h4>
                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col xs={3} className="text-start">
                                        <Link to={'/all_firms'}>
                                            <Button id="but_color">All Firms</Button>
                                        </Link>
                                    </Col>
                                </Row>

                                <Row className="allfirms_table_row py-5 justify-content-center">
                                    <Col xs={12} md={8}>
                                        <Form>
                                            <FormGroup className="mb-3" controlId="formFirmName">
                                                <FormControl
                                                    type="text"
                                                    placeholder="Firm Name"
                                                    value={firmName}
                                                    onChange={(e) => setFirmName(e.target.value)}
                                                />
                                            </FormGroup>

                                            <FormGroup className="mb-3" controlId="formEmailID">
                                                <FormControl
                                                    type="email"
                                                    placeholder="Email address"
                                                    value={firmEmail}
                                                    onChange={(e) => setFirmEmail(e.target.value)}
                                                />
                                            </FormGroup>

                                            <FormGroup className="mb-3" controlId="formAddress">
                                                <FormControl
                                                    type="text"
                                                    placeholder="Address"
                                                    value={firmAddress}
                                                    onChange={(e) => setFirmAddress(e.target.value)}
                                                />
                                            </FormGroup>

                                            <FormGroup className="mb-3" controlId="formGSTNumber">
                                                <FormControl
                                                    type="text"
                                                    placeholder="GST Number"
                                                    value={firmGstno}
                                                    onChange={(e) => setFirmGstno(e.target.value)}
                                                />
                                            </FormGroup>

                                            <FormGroup className="mb-3" controlId="formStatus">
                                                <FormControl
                                                    type="text"
                                                    placeholder="Status"
                                                    disabled
                                                />
                                            </FormGroup>

                                            <Row className="justify-content-center align-content-center">
                                                <Button onClick={create_firm} className='col-md-3' id='but_color'>Save</Button>
                                                {errorMessage && <div className="text-danger mt-2 text-center">{errorMessage}</div>}
                                                {successMessage && <div className="text-success mt-2 text-center">{successMessage}</div>}
                                            </Row>
                                        </Form>
                                    </Col>
                                </Row>
                            </div>
                        </Col>
                    </Row>
                </div>
            </div>
        </div>
    );
}

export default Create_New_Firm;
